/**
 * Using Rails-like standard naming convention for endpoints.
 * GET     /api/lastPages              ->  index
 * POST    /api/lastPages              ->  create
 * GET     /api/lastPages/:id          ->  show
 * PUT     /api/lastPages/:id          ->  upsert
 * PATCH   /api/lastPages/:id          ->  patch
 * DELETE  /api/lastPages/:id          ->  destroy
 */

'use strict';

import jsonpatch from 'fast-json-patch';
import LastPage from './lastPage.model';
import _ from 'lodash';

function respondWithResult(res, statusCode) {
  statusCode = statusCode || 200;
  return function(entity) {
    if(entity) {
      return res.status(statusCode).json(entity);
    }
    return null;
  };
}

function patchUpdates(patches) {
  return function(entity) {
    try {
      jsonpatch.apply(entity, patches, /*validate*/ true);
    } catch(err) {
      return Promise.reject(err);
    }

    return entity.save();
  };
}

function removeEntity(res) {
  return function(entity) {
    if(entity) {
      return entity.remove()
        .then(() => {
          res.status(204).end();
        });
    }
  };
}

function handleEntityNotFound(res) {
  return function(entity) {
    if(!entity) {
      res.status(404).end();
      return null;
    }
    return entity;
  };
}

function handleError(res, statusCode) {
  statusCode = statusCode || 500;
  return function(err) {
    res.status(statusCode).send(err);
  };
}

// Gets a list of LastPages
export function index(req, res) {
  return LastPage.findOne().exec()
    .then(respondWithResult(res))
    .catch(handleError(res));
}

// Gets a single LastPage from the DB
export function show(req, res) {
  return LastPage.findById(req.params.id).exec()
    .then(handleEntityNotFound(res))
    .then(respondWithResult(res))
    .catch(handleError(res));
}

// Creates a new LastPage in the DB
export function create(req, res) {
  if (req.user._id){
    var lastPageObject = _.merge({name: req.body.name},{user: req.user._id});
    console.log(req.user);
    return LastPage.findOneAndUpdate({_id: req.user._id}, lastPageObject, {upsert: true}).exec()

    .then(respondWithResult(res))
    .catch(handleError(res));
  }
}

// Upserts the given LastPage in the DB at the specified ID
export function upsert(req, res) {
  if(req.body._id) {
    delete req.body._id;
  }
  return LastPage.findOneAndUpdate({_id: req.params.id}, req.body, {upsert: true, setDefaultsOnInsert: true, runValidators: true}).exec()

    .then(respondWithResult(res))
    .catch(handleError(res));
}

// Updates an existing LastPage in the DB
export function patch(req, res) {
  if(req.body._id) {
    delete req.body._id;
  }
  return LastPage.findById(req.params.id).exec()
    .then(handleEntityNotFound(res))
    .then(patchUpdates(req.body))
    .then(respondWithResult(res))
    .catch(handleError(res));
}

// Deletes a LastPage from the DB
export function destroy(req, res) {
  return LastPage.findById(req.params.id).exec()
    .then(handleEntityNotFound(res))
    .then(removeEntity(res))
    .catch(handleError(res));
}
